<template>
	<template-auth 
    titulo="Alterar senha"
    :progressLoader="progressLoader"
  >
    <form @submit.prevent="cadastrar">
      <v-text-field
        ref="senha"
        id="password"
        :type="exibirSenha ? 'text' : 'password'"
        v-model="senha"
        label="Informe sua nova senha"
        :rules="passwordRules"
        :append-icon="exibirSenha ? 'visibility_off' : 'remove_red_eye'"
        @click:append="exibirSenha = !exibirSenha"
        v-on:keyup.enter="focusToNext('confimarSenha')"
      />
      
      <v-text-field
        ref="confimarSenha"
        :type="exibirSenha2 ? 'text' : 'password'"
        v-model="confirmaSenha"
        label="Confirmar Senha"
        :rules="passwordRules"
        :append-icon="exibirSenha2 ? 'visibility_off' : 'remove_red_eye'"
        @click:append="exibirSenha2 = !exibirSenha2"
        v-on:keyup.enter="focusToNext('code')"
      />

      <v-text-field
        ref="code"
        v-model="code"
        label="Informe o código de confirmação"
        maxLength="4"
        append-icon="lock"
        v-on:keyup.enter="enterClick"
      />
  
      <div class="text-center mt-4">
        <btn-confirm
          text="Alterar Senha"
          :disabled="btnDisabled"
          @clicked="cadastrar"
        />
      </div>
    </form>
  </template-auth>
</template>

<script>
// Modules
import Axios from "@/plugins/axios"
// Components
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import TemplateAuth from '@/pages/auth/template/Template'
// Mixins and MOdules
import FormMixin from '@/mixins/FormMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'ChangePassword',
  components: { BtnConfirm, TemplateAuth },
  mixins: [ FormMixin, SnackbarMixin, NavigationMixin, ValidationMixin ],
  data: () => ({
    progressLoader: false,
    exibirSenha: false,
    exibirSenha2: false,
    senha: '',
    confirmaSenha: '',
    code: null  
  }),
  computed: {
    btnDisabled () {
      if (!this.isPasswordValid(this.senha)) return true
      else if (!this.isPasswordValid(this.confirmaSenha)) return true
      else if (this.senha !== this.confirmaSenha) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#password').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnDisabled) this.cadastrar()
      else this.showSnackbarErro('Preencha todos os dados corretamente!')
    },
    cadastrar () {
      this.progressLoader = true
      let token = this.$route.params.token

      Axios.post('/auth/barber/change-password', { 
        token: token,
        password: this.senha,
        code: this.code 
      })
      .then(response => {
        if (response.data.codigo === 200) {
          this.progressLoader = false
          this.showSnackbar('Senha alterada com sucesso!')
          this.goTo('login')
        }
        else this.setErro(response.data.corpo)
      })
      .catch (() => { this.setErro('Não foi possível alterar a senha!') })
    },
    setErro (msg) {
      this.progressLoader = false
      this.showSnackbarErro(msg)
    }
  }
}
</script>